<template>
  <div>
    <Head></Head>
    <el-row type="flex" justify="center">
      <el-col :span="20">
        <el-carousel :interval="4000" arow="always" :height="height">
          <el-carousel-item v-for="(item,index) in list" :key="index">
            <div class="demo-image" id="imgbg">
              <el-image style="wdith:800px" fit="contain" :src="'/static/img/'+item.image" alt="" :key="'/static/img/' + item.image"></el-image>
            </div>

          </el-carousel-item>
        </el-carousel>




      </el-col>
    </el-row>
    <van-rate v-model="value" allow-half void-icon="star" void-color="#eee" />

    <img v-if="btnFlag" @click="backTop" class="go-top right" style="width:30px;height:30px;" src="../../static/img/backtop.png"
      alt="">

    <span>{{ num }}</span>
    <span>{{ num2 }}</span>
    <div class='box'></div>
    <button @click='cat()'>获取admins数据</button>
    <img src="+src+" >
    <p>upload</p>

    <img  :src="imgData"/>
    左边第一个
    {{ imgData }}

     <img src='data:image/gif;base64,' />
    <van-uploader v-model="fileList" multiple  :max-size="maxs" :after-read="afterRead" />
    <Foot></Foot>
  </div>
</template>

<script>
  import Head from '@/components/Head'
  import Foot from '@/components/Foot'
  import axios from 'axios'
  //1.结构赋值引入vuex
  //1.1 导入
  import {
    mapState,
    mapGetters
  } from 'vuex'
  export default {
    data() {

      const item = {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      };
      return {
        maxs:1000000,
        imgSrc1:"",
        imgSrc:"data:image/gif;base64,",
        src:'',
        maxSize: 1000,
        imgData: '',
        fileList: [

        ],
        value: 2.5,
        list: [{
            "image": "image-01.jpg",
            "name": "悲华在线1"
          },



          {
            "image": "image-02.jpg",
            "name": "悲华在线2"
          },
          {
            "image": "image-03.jpg",
            "name": "悲华在线3"
          },
          {
            "image": "image-04.jpg",
            "name": "悲华在线4"
          },
          {
            "image": "image-05.jpg",
            "name": "悲华在线5"
          }

        ],
        height: '',
        tableData: Array(20).fill(item),
        btnFlag: false
      }
    },
    beforeMount: function() {
      if ($(window).width() < 700) {
        this.height = $(window).width() * 2 / 3 + 'px';
      } else {
        this.height = $(window).width() * 7 / 15 + 'px';
      }

    },
    //1.2computed引入
    computed: mapState({
      // //1.2.1箭头函数
      // num:state => state.num,
      // //1.2.2
      // num(state){
      //   return num += 50
      // }
      ...mapState(['num']),
      ...mapGetters({
        num2: 'filterNum'
      })
    }),

    mounted() {
      window.addEventListener('scroll', this.scrollToTop)
    },
    destroyed() {
      window.removeEventListener('scroll', this.scrollToTop)
    },
    methods: {
      // 返回布尔值
      beforeRead(file) {
        if (file.type !== 'image/jpeg') {
          Toast('请上传 jpg 格式图片');
          return false;
        }

        return true;
      },

      // 返回 Promise
      asyncBeforeRead(file) {
        return new Promise((resolve, reject) => {
          if (file.type !== 'image/jpeg') {
            Toast('请上传 jpg 格式图片');
            reject();
          } else {
            resolve();
          }
        });
      },
      afterRead(file) {
        // 此时可以自行将文件上传至服务器
        console.log(file.file.name);
        let myDate = new Date().getTime() + 28800000
        let myDate1 = new Date(myDate)
        let data = {
          "content": file.content,
          "lastModifiedDate": myDate1,
          "name": file.file.name,
          "size": file.file.size,
          "type": file.file.type
        }

        axios.post("/api/pictures/picture", data).then((result) => {
          let res = result.data
          this.imgData = res.result.content
          this.src = res.result.content00000
          console.log(res.result.content)
          if (res.status == 0) {
            console.log('图片上传成功！')

          } else {
            console.log('图片上传失败！')
          }
        })
        console.log(data)
      },
      cat() {
        axios.post('/api/goods/userMsg').then((result) => {
          let res = result.data
          if (res.status == 0) {
            alert('获取成功！')
          } else {
            alert('获取失败！')
          }
        })
      }

      ,
      backTop() {
        const that = this
        let timer = setInterval(() => {
          let ispeed = Math.floor(-that.scrollTop / 5)
          document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed
          if (that.scrollTop === 0) {
            clearInterval(timer)
          }
        }, 16)
        // document.documentElement.scrollTop = document.body.scrollTop = 0
      },
      scrollToTop() {
        const that = this
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        that.scrollTop = scrollTop
        if (that.scrollTop > 60) {
          that.btnFlag = true
        } else {
          that.btnFlag = false
        }


      }
    },
    components: {
      Head,
      Foot
    }
  }
</script>

<style scoped>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 5%;
  }
  .box{background-image:url(data:image/png;base64,);}
  .el-aside {
    color: #333;
  }

  .go-top {
    position: fixed;
    bottom: 40px;
    right: 30px;

  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  #imgbg {
    background-color: #fff;
  }
</style>



